/* Dark overlay behind the popup */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5); /* Semi-transparent overlay */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000; /* Ensure it appears above other content */
}

/* Popup dialog styling */
.modal-popup {
  position: relative;
  background: white;
  padding: 20px;
  width: 300px;
  border-radius: 8px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 18px;
  font-weight: bold;
  color: #555;
  cursor: pointer;
}

.close-button:hover {
  color: #ff4c4c;
}

/* Optional styling for the link button inside the dialog */
.link-button {
  background: none;
  border: none;
  color: #007ACC;
  cursor: pointer;
  text-decoration: underline;
  font-size: 14px;
  margin: 10px 0;
}

/* Input styling with responsive width */
.api-input {
  width: 100%;
  padding: 8px;
  margin-right: 5px;
  box-sizing: border-box;
  background-color: #f1f1f1; /* Light gray background */
  color: #333333; /* Dark text for readability */
  border: 1px solid #ccc; /* Soft gray border */
  border-radius: 20px; /* Optional: Rounded corners */
}

/* Input hover effect */
.api-input:hover {
  background-color: #e6e6e6; /* Slightly darker background on hover */
  border-color: #007bff; /* Change border color to blue */
}

/* Input focus state */
.api-input:focus {
  border-color: #007bff; /* Blue border on focus */
  background-color: #e6f0ff; /* Light blue background on focus */
  outline: none; /* Remove default focus outline */
}

/* savecancel button */
.savecancel-button {
  width: 80px;
  padding: 8px 12px;
  margin-right: 5px;
  color: #333333; /* Text color */
  border: 1px solid #ccc; /* Soft border */
  border-radius: 20px; /* Optional: Rounded corners */
  cursor: pointer; /* Pointer cursor to indicate button is clickable */
}

.savecancel-button:first-of-type {
  background-color: #B0CEFF;  /* Color for Save button */
}

.savecancel-button:last-of-type {
  background-color: #C3C3C3;  /* Color for Cancel button */
}

/* Button hover effect */
.savecancel-button:hover {
  background-color: #e6e6e6; /* Slightly darker background on hover */
  color: #007bff; /* Change text color to blue on hover */
  border-color: #007bff; /* Match border to text color on hover */
}

/* Button focus state */
.savecancel-button:focus {
  border-color: #007bff; /* Blue border on focus */
  background-color: #e6f0ff; /* Light blue background on focus */
  outline: none; /* Remove default focus outline */
}

.button-row {
  display: flex;
  justify-content: center;  /* Aligns buttons to the right */
  gap: 10px;                  /* Space between Save and Cancel buttons */
  margin-top: 10px;           /* Adds some space above the buttons */
}